.widgetSm {
    flex:1;
    -webkit-box-shadow: 0px 0px 15px -10px rgba(0,0,0,0.75); 
    box-shadow: 0px 0px 15px -10px rgba(0,0,0,0.75);
    padding: 20px;
    margin-right: 20px;
}

.widgetSmTitle {
    font-size: 22px;
    font-weight: 600;
}
.widgetSmList{
    margin: 0;
    padding: 0;
    list-style: none;

}
/* .widgetSmListItem {
    display:flex;
    align-items: center;
    justify-content: space-between;


} */

.widgetSmListItem {
    display:grid;
    grid-template-columns: 1fr 2fr 1fr;
    align-items: center;
}


.widgetSmImg {
    height:40px;
    width:40px;
    border-radius: 50%;
    object-fit: cover;
    margin:20px 0;

}
.widgetSmUser {
    display: flex;
    flex-direction: column;
}
.widgetSmUserName{
    font-weight: 600;
}

.widgetSmUserTitle {
    font-weight: 300;
}
.widgetSmButton {
    display: flex;
    align-items: center;
    border:none;
    border-radius: 10px;
    padding: 7px 10px;
    color:#555;
    background: #eeeef7;
    cursor: pointer;
}
.widgetSmIcon {
    font-size:15px;
    margin-right: 5px;
}
