.widgetLg {
    flex:2;
    -webkit-box-shadow: 0px 0px 15px -10px rgba(0,0,0,0.75); 
    box-shadow: 0px 0px 15px -10px rgba(0,0,0,0.75);
    padding: 20px;
}
.widgetLgTitle {
    font-size: 22px;
    font-weight: 600;
}
.widgetLgTable {
    width:100%;
    border-spacing: 25px;
}
.widgetLgTh {
    text-align: left;
}
.widgetUser {
    display: flex;
    align-items: center;
    font-weight: 600;
}


.widgetLgImg {
    height:40px;
    width:40px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}
.widgetLgDate, .widgetLgAmount {
    font-weight: 300;
}
.widgetButton {
    padding: 5px 7px;
    border: none;
    border-radius: 10px;
}
.widgetButton.Approved {
    background-color: #e5faf2 ;
    color:#3bb077 ;
}
.widgetButton.Pending {
    background-color:#fff0f1;
    color: #d95087;
}
.widgetButton.Cancelled {
    background-color:#ebf1fe;
    color: #2a7ade;
}