.product {
    flex: 4;
    padding: 20px;
}
.productTitleContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.productAddBtn {
    width: 80px;
    border: none;
    padding: 5px;
    background-color: teal;
    color:white;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;

}
.productTop {
    display: flex;
}
.productTopLeft, 
.productTopRight {
    flex:1;
}

.productTopRight {
    padding: 20px;
    margin: 20px;
    -webkit-box-shadow: 0px 0px 15px -10px rgba(0,0,0,0.75); 
    box-shadow: 0px 0px 15px -10px rgba(0,0,0,0.75);
}
.productInfoTop {
    display: flex;
    align-items: center;
}
.productInfoImg {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 20px;
}
.productName {
    font-weight: 600;
}
.productInfoBottom {
    margin-top: 10px;

}
.productinfoItem {
    width: 150px;
    display: flex;
    justify-content: space-between;

}
.productInfoValue {
    font-weight: 300;
}

.productBottom {
    padding: 20px;
    margin: 20px;
    -webkit-box-shadow: 0px 0px 15px -10px rgba(0,0,0,0.75); 
    box-shadow: 0px 0px 15px -10px rgba(0,0,0,0.75); 

}
.productForm {
    display: flex;
    justify-content: space-between;

}
.productFormLeft {
    display: flex;   
    flex-direction: column;

}
.productFormLeft>label {
    margin-bottom: 10px;
    color:gray;
}
.productFormLeft>input {
    margin-bottom: 10px;
    border: none;
    padding: 5px;
    border-bottom: 1px solid gray;
}
.productFormLeft>select {
    margin-bottom: 10px;
}

.productFormRight {
    width:540px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.productUpload {
    display: flex;
    align-items: center;
}
.productUploadImg {
    width: 100px;
    height: 120px;
    border-radius: 10px;
    object-fit: cover;
    margin-right: 20px;

}
.productBtn {
    border: none;
    padding: 5px;
    border-radius: 5px;
    background: darkblue;
    color: white;
    font-weight: 600;
    cursor: pointer;
    width: 100px;
}